import React from 'react'; import styled from 'styled-components'; 
// --------------
import MainText from './Components/HeaderInfo';

const Main = (props) => {
        return (
                <MainWR>
                        <HeaderWR> <div id='status'>asd</div> 
                        <div id="fb-root">asd</div>
                        <div className="fb-login-button" data-size="large" data-button-type="continue_with" data-layout="default" data-auto-logout-link="false" data-use-continue-as="false" data-width=""></div>
                        <MainText /> </HeaderWR>
                </MainWR>
        )
}

const MainWR = styled.div `display: flex; align-items: center; justify-content: center; 
padding: 10px; width: 100%; min-height: 100%; background: #fff; overflow: auto; font-family: 'Architects Daughter', cursive;`
// -------------------------------------------------------------------------------------------
const HeaderWR = styled.div `padding-bottom: 100px;`
// -------------------------------------------------------------------------------------------


export default Main